.questions {
  max-width: 970px;
  margin: 0 auto;
  padding-top: 35px;

  @media (max-width: 1023px) {
      width: 100%;
      padding: 10px 45px 0;
      box-sizing: border-box;
  }

  @media (max-width: 767px) {
      padding: 0 15px;
  }
}

.questions h2 {
  margin: 0;
  padding: 35px 0;
  font-family: "Cormorant Garamond", "Georgia", serif;
  font-weight: bold;
  font-size: 34px;
  letter-spacing: 0.03em;
  text-align: center;

  @media (max-width: 767px) {
      line-height: 44px;
      padding: 30px 0;
  }
}

.questions__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 25px 0;

  @media (max-width: 767px) {
      margin-top: 0;
  }
}

.questions__item {
  position: relative;
  min-height: 112px;
  padding: 45px 42px 42px;
  border: 1px solid $brown;
  border-bottom: none;
  box-sizing: border-box;
  cursor: pointer;

  &:last-child {
      border-bottom: 1px solid $brown;
  }

  @media (max-width: 1023px) {
      padding-left: 48px;
  }

  @media (max-width: 767px) {
      min-height: 83px;
      padding: 15px 55px 18px 25px;
  }

  &::before {
      position: absolute;
      content: "";
      width: 15px;
      height: 8px;
      right: 50px;
      top: 50px;
      background-image: url("../img/faq-icon.svg");
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat;

      @media (max-width: 1023px) {
          width: 23px;
          height: 12px;
      }

      @media (max-width: 767px) {
          width: 20px;
          height: 10px;
          right: 25px;
          top: 25px;
      }
  }

  &--nojs::before {
      display: none;
  }
}

.questions__item--active::before {
  transform: rotate(180deg);
  transition: transform, 0.3s;
}

.questions__item:hover h3,
.questions__item:focus h3 {
  color: $brown;
}

.questions__item:active h3 {
  opacity: 0.7;
}

.questions__item h3 {
  margin: 0;
  font-weight: 500;
  font-size: 18px;

  br {
      display: none;
  }

  @media (max-width: 767px) {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: -0.01em;

      br {
          display: block;
      }
  }
}

.questions__item p {
  display: none;
  max-width: 525px;
  margin: 0;
  padding-top: 10px;
  padding-left: 10px;
  font-size: 16px;
  line-height: 21px;

  @media (max-width: 1023px) {
      max-width: 530px;
      padding-top: 27px;
      padding-left: 0;
  }

  @media (max-width: 767px) {
      padding-top: 15px;
      font-size: 14px;
      line-height: 20px;
  }
}

.questions__item--active p,
.questions__item--nojs p {
  display: block;
}
