.catalog__filter form {
  min-width: 250px;
  margin-right: 50px;
}
@media (max-width: 1023px) {
  .catalog__filter form {
    display: none;
  }
}

.catalog__filter--active form {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  max-height: 100vh;
  width: 425px;
  padding: 25px 0;
  background: #FFFFFF;
  box-shadow: 0 5px 32px rgba(219, 213, 208, 0.24);
  overflow-y: scroll;
  z-index: 20;
}
@media (max-width: 767px) {
  .catalog__filter--active form {
    width: 100%;
    max-width: 425px;
    padding: 70px 25px;
  }
}

@media (max-width: 1023px) {
  .catalog__products {
    padding-top: 50px;
  }
}
@media (max-width: 767px) {
  .catalog__products {
    padding-top: 40px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 35px;
}
@media (max-width: 767px) {
  .catalog__list {
    margin-bottom: 15px;
  }
}

.catalog__item {
  width: calc((100% - 60px) / 3);
  margin-right: 30px;
  margin-bottom: 30px;
  text-align: center;
}
.catalog__item:nth-child(3n) {
