.questions__item p {
  display: none;
  max-width: 525px;
  margin: 0;
  padding-top: 10px;
  padding-left: 10px;
  font-size: 16px;
  line-height: 21px;
}
@media (max-width: 1023px) {
  .questions__item p {
    max-width: 530px;
    padding-top: 27px;
    padding-left: 0;
  }
}
@media (max-width: 767px) {
  .questions__item p {
    padding-top: 15px;
    font-size: 14px;
    line-height: 20px;
  }
}

.questions__item--active p,
.questions__item--nojs p {
  display: block;
}